main {
	grid-column-start: 2;
}
#skills {

	ul {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 8px;
		margin-bottom: 2rem;
	}
}
#job-history-1 {
	h3 {
		margin-bottom: 0;
	}
	p {
		margin-top: .2rem;
	}
	li{
		margin: .5rem;
	}
}